@import url('https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600&display=swap');

body,html{
    margin: 0;
    padding: 0;
    font-size: 15px;
    line-height: 26px;
     color: #333;
     font-family: 'Poppins', sans-serif;
 
}

*{
    box-sizing: border-box;
}

.container_fluid{
    width: 100%;
    height: auto;
    display: flex;
    flex-wrap: wrap;
}
.container{
    width: 90%;
    height: auto;
    display: flex;
    flex-wrap: wrap;
    margin: 0 auto;
}

:root{
    --fcolor : #9b3b00;
    --paracolor : #595959;
}

/*************** Header ********************/

.header{
    width: 100%;
    height: auto;
    background-image: url('../src/images/header_bg.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
 
}

.border_bottom{
    border-bottom: 1px solid #f5dda3;
}
.navbar{
    width: 90%;
    height: auto;
    margin: 0 auto;
    display: flex;
    padding: 10px 0;
    
}
.logo{
    width: 20%;
    height: auto;
    padding: 0;
    display: flex;
}

.logo img{
    width: 4rem;
}
.nav{
    width: 80%;
    height: auto;
    display: flex;
    justify-content: flex-end;
}
.nav ul{
    margin: 0;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    list-style-type: none;
}

.nav ul li{
    margin-left: 39px;
}
.nav ul li a{
    text-decoration: none;
    font-size: 14px;
    text-transform:uppercase;
    font-weight: 500;
    color: #fff;
    padding: 5px 0;
    transition: all 0.5s;
}

.nav ul li a.active{
  color: var(--fcolor);
  border-bottom: 2px solid var(--fcolor);
}

.nav ul li a:hover{
    color: var(--fcolor);
    border-bottom: 2px solid var(--fcolor); 
}


/********************* Hero **************************/

.hero{
    width: 100%;
    height: auto;
    padding: 47px 0;
}

.caption{
    width: 50%;
    height: auto;
    padding: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}

.caption h1{
    font-size: 65px;
    font-weight: 500;
    font-family: 'Fredoka One', cursive;
    color: #090301;
    margin: 0 0 37px 0;
    padding: 0;
}

.caption h2{
    font-size: 21px;
    font-weight: 500;
    color: #090301;
    margin: 0;
    padding: 0;
    text-transform: capitalize;
}

.caption h3{
    font-size: 28px;
    font-weight: 500;
    color: #fff;
    margin: 10px 0;
    padding: 0;
    text-transform: capitalize;
}

.img_res{
    width: 100%;
    height: auto;
}

.btn{
    padding: 12px 26px;
    background-color: #fff;
    border-radius: 50px;
    color: #090301;
    border: none;
    margin: 30px 0;
    cursor: pointer;
    font-size: 15px;

}

.btn:hover{
    background-color: var(--fcolor);
    color: #fff;
}

/******************* Best ************************/

.best{
    width: 100%;
    height: auto;
    padding: 40px 0;
    background-color: #fff;
}

.best_wrap{
    width: 90%;
    height: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    margin: 0 auto;
}

.best_wrap h4{
    font-size: 32px;
    display: flex;
    font-weight: 500;
    margin: 20px 0;
    align-items: center;
    font-family: 'Fredoka One', cursive;
}
.best_wrap span{
    width: 20px;
    height: 20px;
    border-radius: 50px;
    background-color: #ebbf60;
    margin: 5px 14px;
}

.best_wrap p{
    font-size: 15px;
    color: var(--paracolor);
}

/************************* Rings ******************************/

.rings{
    width: 100%;
    height: auto;
    padding: 40px 0;
   

}

.ring_wrap{
    width: 100%;
    height: auto;
    display: flex;
    position:relative;

}

.ring_wrap::before{
    content: "";
    width: 93%;
    height: 380px;
    background-color: #ebebeb;
    border-radius: 191px;
    transform: rotate(140deg);
    margin: 90px auto 0 auto;
    position: absolute;
    right: 65px;
    top: 103px;
    z-index: -1;
    
}

.box_content{
    width: 55%;
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
 }

 .box_content:nth-child(2){
     width: 45%;
     height: auto;
    
   
 }

.box_content p{
    font-size: 15px;
    color:var(--paracolor);
    
}

.btns{
    padding: 12px 25px;
    font-size: 15px;
    color: #fff;
    border: 2px solid #fce68a;
    border-radius: 50px;
    background-color: #000;
    cursor: pointer;

}

.btns:hover{
    color: #000;
    border: 2px solid #fce68a;
    background-color: #fff;
}

.btngroup{
    width: 100%;
    height: auto;
    text-align: right;
    margin-bottom: 0;
}

.btngroups{
    width: 100%;
    height: auto;
    text-align: left; 
}


.circle{
    width: 340px;
    height: 340px;
    border-radius: 50%;
    background: rgb(254,234,133);
    background: linear-gradient(90deg, rgba(254,234,133,1) 0%, rgba(235,191,96,1) 52%, rgba(235,191,96,1) 100%);
    display: flex;
    align-items: center;
    justify-content: center;
}

.innerCircle{
    width: 290px;
    height: 290px;
    border-radius: 50%;
    background-color: #000;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.innerCircle p{
    font-size: 18px;
    color: #fff;
}



.box_content2{
    width: 45%;
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    padding-left: 50px;
 }

 .box_content2:nth-child(2){
     width: 55%;
     height: auto;
     display: flex;
     flex-direction: column;
     justify-content: center;
     align-items: center;
     padding-left: 0;
    
   
 }

 .ring_wrap2{
    width: 100%;
    height: auto;
    display: flex;
    position:relative;
     margin-top: 97px;

}

.ring_wrap2::before{
    content: "";
    width: 93%;
    height: 380px;
    background-color: #ebebeb;
    border-radius: 191px;
    transform: rotate(41deg);
    margin: 90px auto 0 auto;
    position: absolute;
    left: 13px;
    top: 118px;
    z-index: -1;
}

.ring_wrap3{
    width: 100%;
    height: auto;
    display: flex;
    position: relative;
    margin-top: 108px;
}

.img1{
    width: 7rem;
}

.img2{
    width: 5rem;
}

.img3{
    width: 9rem;
}

/************************* Rings ******************************/
.request{
    width: 100%;
    height: auto;
    padding: 84px 0;

}

.request_wrap{
    width: 100%;
    height: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    margin-bottom: 40px;
    
}

.request_wrap h5{
    font-size: 32px;
    display: flex;
    font-weight: 500;
    margin: 20px 0;
    align-items: center;
    font-family: 'Fredoka One', cursive;
}

.form_box{
    width: 50%;
    height: auto;
}

.form_box img{
    width: 100%;
    height: auto;
}

.form{
    width: 100%;
    height: auto;
    padding: 32px 37px;
    border: 5px solid #ecc062;
    border-radius: 25px;
    
}

.inputform{
    width: 100%;
    height: auto;
    border: 0;
    border-bottom: 1px solid rgb(213 213 213);
    padding: 10px 0;
}



.form_group{
    width: 100%;
    height: auto;
    margin-bottom: 31px;
}

.btnsend{
    padding: 11px 40px;
    font-size: 17px;
    color: #fff;
    border: none;
    border-radius: 50px;
    background-color: #f2740d;
    cursor: pointer;
    border: 1px solid transparent;
}

.btnsend:hover{
    background-color: #fff;
    border: 1px solid #f2740d;
    color: #f2740d;
}

.center{
    text-align: center;
}

/********************** clentreview *******************************/

.clentreview{
    width: 100%;
    height: auto;
    padding: 40px 0 96px;
    text-align: center;
}

.client_wrap{
    width: 100%;
    height: auto;
    margin-bottom: 37px;
    
}

.client_wrap h2{
    font-size: 32px;
     font-weight: 500;
     margin: 20px 0;
    font-family: 'Fredoka One', cursive;

}

.review{
    width: 45%;
    height: auto;
    display: flex;
    align-items: center;
    justify-content: end;
    position: relative;
   
}

.review::after{
    content: "";
    width: 30px;
    height: 144px;
    background-color: #000;
    position: absolute;
    right: 0;
    top: 108px;
    z-index: -1;

}


.review:last-child{
    width: 55%;
    height: 35vh;
    display: flex;
    flex-direction: column;
    align-items: baseline;
    justify-content: center;
    
   
}

.r_circle{
    width: 340px;
    height: 340px;
    border-radius: 50%;
    background: rgb(254,234,133);
    background: linear-gradient(90deg, rgba(254,234,133,1) 0%, rgba(235,191,96,1) 52%, rgba(235,191,96,1) 100%);
    display: flex;
    align-items: center;
    justify-content: center;
   
}


.r_innerc{
    width: 290px;
    height: 290px;
    border-radius: 50%;
    background-color: #000;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.icons{
    margin-right: 30px;
}

.icons .quotes{
    font-size: 42px;
    color:#ebbf60;
}

.review h6{
    font-size: 20px;
    text-transform: capitalize;
    color: #ecc062;
    margin: 0px 0 14px 20px;
}

.review p{
    padding: 20px;
    color: #fff;
    background-color: #000;
    font-size: 16px;
    margin: 0;
    text-align: left;
}


/***************************** footer ******************************/

.footer{
    width: 100%;
    height: auto;
    padding: 40px 0;
    background: rgb(254,234,133);
     background: linear-gradient(180deg, rgba(254,234,133,1) 0%, rgba(235,191,96,1) 100%, rgba(235,191,96,1) 100%);
}

.footer_wrap{
    width: 100%;
    height: auto;
    border-top: 1px solid #fdf6e7;
    text-align: center;
    display: flex;
    padding: 28px 0;
}

.footer_box{
    width: 50%;
    height: auto;
    padding: 0;
    text-align: left;
    
}

.footer_box h1{
    font-size: 25px;
    color: #fff;
    font-weight: 400;
    font-family: 'Fredoka One', cursive;
    margin: 20px 0;
}

.footer_box h2{
   margin: 0;
    
}

.footer_box h3{
    font-size: 30px;
    color: #333;
    font-weight: 400;
    margin: 20px 0;
    
}
.footer_box h4{
    font-size: 35px;
    color: #fff;
    font-weight: 400;
    font-family: 'Fredoka One', cursive;
    margin: 0;
}

.footer_bottom{
    width: 100%;
    height: auto;
    background: rgb(254,234,133);
    background: linear-gradient(180deg, rgba(254,234,133,1) 0%, rgba(235,191,96,1) 100%, rgba(235,191,96,1) 100%);
    padding: 20px 0;
}

.footerb_wrap{
    width: 100%;
    height: auto;
    text-align: center;
}

.footerb_wrap p{
    font-size: 15px;
    color: #333;
    margin: 0;
    padding: 0;
}



/*********** Media Queries *************/

@media (min-width: 150px) and (max-width: 844px) {
    .container_fluid {
        flex-direction: column;
    }
  
    .container {
        width: 100%;
        height: auto;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .navbar {
        width: 100%;
        display: flex;
        padding: 10px 0;
        flex-direction: column;
    }

    .nav {
        width: 100%;
         display: flex;
        justify-content: center;
    }

    .logo {
        width: 100%;
        display: flex;
        justify-content: center;
        padding: 13px 0;
    }

    .caption {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
    }

    .caption h3 {
        line-height: 39px;
    }

    .ring_wrap {
        flex-direction: column;
    }
    
    .box_content {
        width: 90%;
        height: auto;
        display: flex;
        flex-direction: column;
        justify-content: center;
        text-align: center;
        margin: 0 auto;
    }

    .btngroup {
        width: 100%;
        height: auto;
        text-align: center;
        margin-bottom: 20px;
    }

    .box_content:nth-child(2) {
        width: 90%;
        height: auto;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .ring_wrap2 {
        width: 100%;
        height: auto;
        display: flex;
        position: relative;
        margin-top: 50px;
        flex-direction: column;
    }

    .box_content2 {
        width: 100%;
        height: auto;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding-left: 0px;
    }

    .box_content2:nth-child(2) {
        width: 90%;
        height: auto;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding-left: 0;
        margin: 0 auto;
        text-align: center;
    }

    .btngroups {
        width: 100%;
        height: auto;
        text-align: center;
    }

    .ring_wrap::before {
        content: "";
        display: none;
    }
    .ring_wrap2::before {
        content: "";
       display: none;
    }
    .ring_wrap3 {
        width: 100%;
         margin-top: 108px;
        flex-direction: column;
    }

    .request {
        width: 90%;
        height: auto;
        padding: 28px 0;
        margin: 0 auto;
    }

    .form_box {
        width: 100%;
        height: auto;
    }

    .client_wrap {
        width: 90%;
        height: auto;
        margin-bottom: 37px;
    }
    .review {
        width: 100%;
        height: auto;
        display: flex;
        align-items: center;
        justify-content: end;
        position: relative;
        flex-direction: column;
    }
    .review::after {
        content: "";
        display: none;
    }

    .review:last-child {
        width: 90%;
        height: auto;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin-top: 23px;
    }

    .clentreview {
        width: 100%;
        height: auto;
        padding: 40px 0 69px;
        text-align: center;
    }

    .footer_wrap {
        width: 100%;
        text-align: center;
        display: flex;
        padding: 28px 0;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .footer_box {
        width: 100%;
        height: auto;
        padding: 0;
        text-align: center;
    }

    .nav ul li a {
        margin: 0 12px;
    }
    .nav ul li{
        margin-left: 0;
    }
  }


  @media (min-width: 768px) and (max-width: 1024px) {
  
    .review::after {
        content: "";
        width: 30px;
        height: 144px;
        background-color: #000;
        position: absolute;
        right: 0;
        top: 82px;
        z-index: -1;
    }
    
  }